'use client';

import { useState, useEffect } from 'react';
import { useAppSelector, useAppDispatch } from '@/lib/redux/hooks';
import { clearPosCart } from '@/lib/redux/features/posSlice';
import { Button } from '@/components/ui/button';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Label } from '@/components/ui/label';
import { Trash2, Maximize2, ShoppingBag } from 'lucide-react';
import { removeFromPosCart, updatePosCartQuantity } from '@/lib/redux/features/posSlice';
import { QuickPaymentButtons } from './QuickPaymentButtons';
import Image from 'next/image';
import toast from 'react-hot-toast';
import { cn } from '@/lib/utils';

const TAX_RATE = 0.1; // 10% tax
const DISCOUNT = 10; // $10 discount for demo

export function OrderSummary() {
  const dispatch = useAppDispatch();
  const cart = useAppSelector((state) => state.pos.cart);
  const orderType = useAppSelector((state) => state.pos.orderType);
  const locationId = useAppSelector((state) => state.pos.locationId);
  const cashierId = useAppSelector((state) => state.pos.cashierId);
  
  const [selectedPaymentMethod, setSelectedPaymentMethod] = useState<'CASH' | 'CARD' | 'DIGITAL_WALLET' | 'SPLIT'>('CASH');
  const [cashAmount, setCashAmount] = useState<number | null>(null);
  const [isProcessing, setIsProcessing] = useState(false);
  const [orderNumber, setOrderNumber] = useState<string>(`#${Date.now().toString().slice(-6)}`);

  const subtotal = cart.reduce((sum, item) => sum + item.price * item.quantity, 0);
  const tax = subtotal * TAX_RATE;
  const discount = DISCOUNT;
  const total = subtotal + tax - discount;
  const change = cashAmount && cashAmount >= total ? cashAmount - total : 0;

  const handleRemove = (itemId: string) => {
    dispatch(removeFromPosCart(itemId));
  };

  const handleQuantityChange = (itemId: string, quantity: number) => {
    if (quantity <= 0) {
      dispatch(removeFromPosCart(itemId));
    } else {
      dispatch(updatePosCartQuantity({ id: itemId, quantity }));
    }
  };

  // Load last payment method from localStorage
  useEffect(() => {
    const lastMethod = localStorage.getItem('pos_last_payment_method');
    if (lastMethod && ['CASH', 'CARD', 'DIGITAL_WALLET', 'SPLIT'].includes(lastMethod)) {
      setSelectedPaymentMethod(lastMethod as any);
    }
  }, []);

  // Keyboard shortcut: Enter to place order
  useEffect(() => {
    const handleKeyPress = (e: KeyboardEvent) => {
      if (e.key === 'Enter' && cart.length > 0 && !isProcessing) {
        e.preventDefault();
        handlePlaceOrder();
      }
    };

    window.addEventListener('keydown', handleKeyPress);
    return () => window.removeEventListener('keydown', handleKeyPress);
  }, [cart.length, isProcessing, selectedPaymentMethod, cashAmount, total]);

  const handlePaymentSelect = (method: 'CASH' | 'CARD' | 'DIGITAL_WALLET' | 'SPLIT', amount?: number) => {
    setSelectedPaymentMethod(method);
    if (method === 'CASH' && amount) {
      setCashAmount(amount);
    } else if (method !== 'CASH') {
      setCashAmount(null);
    }
    // Save to localStorage
    localStorage.setItem('pos_last_payment_method', method);
  };

  const handlePlaceOrder = async () => {
    if (cart.length === 0) {
      toast.error('Cart is empty');
      return;
    }

    if (!locationId || !cashierId) {
      toast.error('Location and cashier must be set');
      return;
    }

    // Validate cash payment
    if (selectedPaymentMethod === 'CASH' && (!cashAmount || cashAmount < total)) {
      toast.error('Cash amount must be at least the total amount');
      return;
    }

    setIsProcessing(true);

    try {
      // Create POS order
      const posOrderResponse = await fetch('/api/pos/orders', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          orderType,
          locationId,
          cashierId,
          items: cart.map((item) => ({
            productId: item.productId,
            variantId: item.variantId,
            quantity: item.quantity,
            price: item.price,
          })),
          subtotal,
          tax,
          discount,
          total,
          paymentMethod: selectedPaymentMethod,
          paymentDetails: selectedPaymentMethod === 'CASH' && cashAmount
            ? { cashAmount, change }
            : null,
        }),
      });

      if (!posOrderResponse.ok) {
        throw new Error('Failed to create POS order');
      }

      const posOrder = await posOrderResponse.json();

      // Complete the order (sync to main Order system)
      const completeResponse = await fetch(`/api/pos/orders/${posOrder.id}/complete`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          items: cart.map((item) => ({
            productId: item.productId,
            variantId: item.variantId,
            quantity: item.quantity,
            price: item.price,
          })),
          subtotal,
          tax,
          discount,
          total,
        }),
      });

      if (!completeResponse.ok) {
        throw new Error('Failed to complete order');
      }

      const { order } = await completeResponse.json();

      // Process payment
      await fetch('/api/pos/payment/process', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          posOrderId: posOrder.id,
          paymentMethod: selectedPaymentMethod,
          amount: total,
          paymentDetails: selectedPaymentMethod === 'CASH' && cashAmount
            ? { cashAmount, change }
            : null,
        }),
      });

      toast.success(`Order ${order.orderNumber} placed successfully!`);
      
      // Clear cart and reset
      dispatch(clearPosCart());
      setCashAmount(null);
      setOrderNumber(`#${Date.now().toString().slice(-6)}`);
      
      // Auto-print receipt if configured (future enhancement)
    } catch (error) {
      console.error('Error processing order:', error);
      toast.error('Failed to process order');
    } finally {
      setIsProcessing(false);
    }
  };

  return (
    <aside className="w-96 border-l bg-background flex flex-col">
      <div className="p-4 border-b flex items-center justify-between bg-primary/5">
        <div>
          <h3 className="font-semibold text-lg">Order's Summary</h3>
          <p className="text-sm text-muted-foreground font-mono">{orderNumber}</p>
        </div>
        <Button variant="ghost" size="icon" className="h-10 w-10">
          <Maximize2 className="h-5 w-5" />
        </Button>
      </div>

      <div className="p-4 border-b bg-muted/30">
        <div className="flex items-center gap-2">
          <ShoppingBag className="h-5 w-5 text-muted-foreground" />
          <p className="text-base font-semibold">Total Items ({cart.length})</p>
        </div>
      </div>

      <ScrollArea className="flex-1 p-4">
        {cart.length === 0 ? (
          <div className="text-center text-muted-foreground py-12">
            <ShoppingBag className="h-12 w-12 mx-auto mb-4 opacity-50" />
            <p className="text-base">Cart is empty</p>
            <p className="text-sm mt-1">Add items to get started</p>
          </div>
        ) : (
          <div className="space-y-3">
            {cart.map((item) => (
              <div
                key={item.id}
                className="flex items-center gap-3 p-3 rounded-lg border hover:bg-muted/50 transition-colors"
              >
                <div className="relative w-16 h-16 rounded-lg overflow-hidden bg-muted flex-shrink-0 border">
                  {item.image && (
                    <Image
                      src={item.image}
                      alt={item.name}
                      fill
                      className="object-cover"
                      sizes="64px"
                    />
                  )}
                </div>
                <div className="flex-1 min-w-0">
                  <p className="text-base font-medium truncate">{item.name}</p>
                  <p className="text-sm text-muted-foreground">
                    ${item.price.toFixed(2)}  {item.quantity} = ${(item.price * item.quantity).toFixed(2)}
                  </p>
                </div>
                <div className="flex items-center gap-1">
                  <Button
                    variant="outline"
                    size="icon"
                    className="h-10 w-10"
                    onClick={() => handleQuantityChange(item.id, item.quantity - 1)}
                    title="Decrease quantity"
                  >
                    <span className="text-lg font-bold"></span>
                  </Button>
                  <div className="w-12 text-center">
                    <span className="text-lg font-bold">{item.quantity}</span>
                  </div>
                  <Button
                    variant="outline"
                    size="icon"
                    className="h-10 w-10"
                    onClick={() => handleQuantityChange(item.id, item.quantity + 1)}
                    title="Increase quantity"
                  >
                    <span className="text-lg font-bold">+</span>
                  </Button>
                  <Button
                    variant="ghost"
                    size="icon"
                    className="h-10 w-10 text-destructive hover:text-destructive hover:bg-destructive/10"
                    onClick={() => handleRemove(item.id)}
                    title="Remove item"
                  >
                    <Trash2 className="h-5 w-5" />
                  </Button>
                </div>
              </div>
            ))}
          </div>
        )}
      </ScrollArea>

      <div className="p-4 border-t space-y-4 bg-muted/20">
        {/* Payment Summary */}
        <div className="space-y-2">
          <div className="flex justify-between text-sm">
            <span className="text-muted-foreground">Price:</span>
            <span className="font-medium">${subtotal.toFixed(2)}</span>
          </div>
          <div className="flex justify-between text-sm">
            <span className="text-muted-foreground">Taxes:</span>
            <span className="font-medium">${tax.toFixed(2)}</span>
          </div>
          <div className="flex justify-between text-sm">
            <span className="text-muted-foreground">Discount:</span>
            <span className="font-medium text-green-600">-${discount.toFixed(2)}</span>
          </div>
          <div className="flex justify-between text-2xl font-bold pt-2 border-t">
            <span>Total:</span>
            <span className="text-primary">${total.toFixed(2)}</span>
          </div>
        </div>

        {/* Quick Payment Buttons */}
        {cart.length > 0 && (
          <div className="space-y-3">
            <Label className="text-sm font-semibold">Payment Method</Label>
            <QuickPaymentButtons
              total={total}
              onPaymentSelect={handlePaymentSelect}
              defaultMethod={selectedPaymentMethod}
            />
          </div>
        )}

        {/* Place Order Button */}
        <Button
          className={cn(
            'w-full h-16 text-lg font-bold',
            cart.length === 0 && 'opacity-50 cursor-not-allowed'
          )}
          size="lg"
          onClick={handlePlaceOrder}
          disabled={cart.length === 0 || isProcessing || (selectedPaymentMethod === 'CASH' && (!cashAmount || cashAmount < total))}
        >
          {isProcessing ? (
            'Processing...'
          ) : (
            <>
              Place Order {cart.length > 0 && `(${cart.length} items)`}
              <span className="ml-2 text-xs font-normal opacity-75">(Enter)</span>
            </>
          )}
        </Button>
      </div>
    </aside>
  );
}

